<script>
export default {
  name: 'IntroSection',

  data() {
    return {
      paragraphs: [
        'Age : 23\nLocation : Aluthgama, Sri Lanka\nEmail : risedirisooriya1994@gmail.com\nContact : +94786889081',
        'I have always been a brilliant problem solver, an innovative visionary, a team player, and a technophile fascinated with cutting-edge technology. I am familiar with a diverse range of programming languages, including C, C++, Java, JavaScript, HTML, CSS, Vue.js, Node.js, Php, and React, as well as SQL and MongoDB, but I am still learning & adding new things to my repertoire. In my spare time, I am learning new things and do some tests to gain my skill and knowledge.',
      ],
      randomParagraph: '',
      sentences: [
        'Hello, I am Rishan',
      ],
      currentIndex: 0,
    };
  },
  computed: {
    words() {
      return this.sentences[this.currentIndex].split(' ');
    }
  },
  mounted() {
    this.generateRandomParagraph();
    this.startTypingAnimation();
    setInterval(this.generateRandomParagraph, 5000);
  },
  methods: {
    generateRandomParagraph() {
      const randomIndex = Math.floor(Math.random() * this.paragraphs.length);
      this.randomParagraph = this.paragraphs[randomIndex];
    },
    startTypingAnimation() {
      setInterval(() => {
        this.currentIndex = (this.currentIndex + 1) % this.sentences.length;
      }, 5000);
    },
    isTyping(index) {
      return index === this.words.length - 1;
    },
  }

}

</script>

<template>
  <div>

        <div class="container">
          <div class="text-card">
             <div class="sentence">
              <span v-for="(word, index) in words" :key="index" :class="{'typing': isTyping(index)}">{{ word }}
              </span>
            </div>
            <h2 class="h2-sen">I am Currently an Associate Software Engineer.</h2>
            <hr style="border: solid; color: white;">
            <div class="paragraph">{{ randomParagraph }}</div>
          </div>
          <div class="image">
            <img src="../assets/myImage.jpeg" class="avatar" alt="Avatar">
          </div>
        </div>
        <hr class="horizontal">
        
  </div>

</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .avatar{
  width: 500px;
  height: 500px;
  border-radius: 50%;
  margin-top: 30px;
  float: right;
  border: 5px solid #C9AFE2;
}

.container {
      display: flex;
      align-items: center;
}
    
    .image {
      flex: 1;
      text-align: right;
    }
    
    .text-card {
      flex: 1;
      width: 300px;
      height: 400px;
    }

.sentence {
  font-size: 50px;
  font-weight: 600;
  font-family: Arial, sans-serif;
  white-space:nowrap;
  overflow: hidden;
  background-image: linear-gradient(95.2deg, rgba(173,252,234,1) 26.8%, rgba(192,229,246,1) 64%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.179);
  border-right: 5px solid;
  border-image: linear-gradient( 95.2deg, rgba(173,252,234,1) 26.8%, rgba(192,229,246,1) 64%) 1;
  border-image-slice: 1;
  animation: typing 3s steps(40) infinite;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 80%;
  }
}

.typing {
  animation: typing 3s steps(40);
  animation-fill-mode: forwards;
  display: inline-block;
  white-space: nowrap;
}

.space {
  display: inline-block;
  width: 0.5em; /* Adjust the space width as desired */
}

.paragraph {
  font-size: 20px;
  font-family: "Times New Roman", Times, serif;
  text-align: justify;
  font-weight: 400;
  margin-bottom: 1em; /* Adjust the margin between paragraphs as desired */
  white-space:pre-line;
  color: white;
}

.h2-sen{
  background-image: linear-gradient(to right, #c6ffdd, #fbd786, #f7797d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.horizontal{
  margin-top: 50px;
  border: 5px solid;
  color: #33001b;
}
</style>