<script>
import VerticalTimeline from './VerticalTimeline.vue';
import { tasks1, tasks2, tasks3 } from './Tasks.js';

export default {
  name: 'EducationSection',
  
  components: {
    VerticalTimeline,
  },

  data() {
    return {
      tasks1,
      tasks2,
      tasks3,
      items: null,
      active: 1
    };
  },
  mounted() {
    this.items = document.querySelectorAll('.slider .item');
    this.loadShow();
  },
  methods: {
    loadShow() {
      let stt = 0;
      this.items[this.active].style.transform = 'none';
      this.items[this.active].style.zIndex = 1;
      this.items[this.active].style.filter = 'none';
      this.items[this.active].style.opacity = 1;

      for (let i = this.active + 1; i < this.items.length; i++) {
        stt++;
        this.items[i].style.transform = `translateX(${120 * stt}px) scale(${
          1 - 0.2 * stt
        }) perspective(16px) rotateY(-1deg)`;
        this.items[i].style.zIndex = -stt;
        this.items[i].style.filter = 'blur(5px)';
        this.items[i].style.opacity = stt > 2 ? 0 : 0.6;
      }
      stt = 0;

      for (let i = this.active - 1; i >= 0; i--) {
        stt++;
        this.items[i].style.transform = `translateX(${-120 * stt}px) scale(${
          1 - 0.2 * stt
        }) perspective(16px) rotateY(1deg)`;
        this.items[i].style.zIndex = -stt;
        this.items[i].style.filter = 'blur(5px)';
        this.items[i].style.opacity = stt > 2 ? 0 : 0.6;
      }
    },
    nextSlide() {
      this.active = this.active + 1 < this.items.length ? this.active + 1 : this.active;
      this.loadShow();
    },
    prevSlide() {
      this.active = this.active - 1 >= 0 ? this.active - 1 : this.active;
      this.loadShow();
    }
  }

}

</script>

<template>
  <div>
        <div class="slider">
          <div class="item img1">
            <div class="transparent-box">
              <h3 class="card-head">SPORTS</h3>
              <vertical-timeline :tasks="tasks1" />
            </div>
          </div>
          <div class="item img2">
            <div class="transparent-box">
              <h3 class="card-head">EDUCATION</h3>
              <vertical-timeline :tasks="tasks2" />  
            </div>
          </div>
          <div class="item img3">
            <div class="transparent-box">
              <h3 class="card-head">PROGRAMING LANGUAGES</h3>
              <vertical-timeline :tasks="tasks3" />  
            </div>
          </div>
          <button id="next" @click="nextSlide">></button>
          <button id="prev" @click="prevSlide">&lt;</button>
        </div>
        <hr class="horizontal">

  </div>

</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .slider {
  position: relative;
  width: 100%;
  height: 370px;
  overflow: hidden;
  margin-top: 70px;
}

.item {
  position: absolute;
  width: 500px;
  height: 350px;
  text-align: justify;
  border-radius: 10px;
  padding: 20px;
  transition: 0.5s;
  left: calc(50% - 250px);
  top: 0;
  
}

.img1{
  background-image: url('../assets/skills.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-radius: 10px;
}

.transparent-box {
  position: absolute;
  border-radius: 10px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.595); /* Adjust the alpha value (0.5) to control the transparency */
}

.img2{
  background-image: url('../assets/education.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.img3{
  background-image: url('../assets/projects.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

#next,
#prev {
  position: absolute;
  top: 40%;
  color: white;
  background-color: transparent;
  border: none;
  font-size: xx-large;
  font-family: monospace;
  font-weight: bold;
  left: 50px;
}

#next {
  left: unset;
  right: 50px;
}

.para{
  padding: 0px 15px 10px 15px;
  text-align: justify;
  color: white;
  font-size: 17px;
  font-weight: 400;
  font-family:'Times New Roman', Times, serif;
}

.card-head{
  padding-top: 5px;
  color: white;
  text-align: center;
}

.horizontal{
  margin-top: 50px;
  border: 5px solid;
  color: #33001b;
}

</style>