// src/router.js
import Vue from "vue";
import VueRouter from "vue-router";
import MyPortfolio from "./components/MyPortfolio.vue";
import MyBlog from './components/MyBlog.vue';
import BlogOne from './components/Blog1.vue';
import BlogTwo from './components/Blog2.vue';
import BlogThree from "./components/Blog3.vue";
import BlogFour from "./components/Blog4.vue";

Vue.use(VueRouter);

const routes = [
  { path: "/", component: MyPortfolio },
  { path: "/blog", component: MyBlog },
  { path: "/blog/1", component: BlogOne },
  { path: "/blog/2", component: BlogTwo },
  { path: "/blog/3", component: BlogThree },
  { path: "/blog/4", component: BlogFour },
];

const router = new VueRouter({
  mode: "history",
  routes,
  scrollBehavior(to) {
    if (to.hash) {
      return {
        selector: to.hash,
        behavior: "smooth",
      };
    }
    return { x: 0, y: 0 };
  },
});

export default router;
