<script>
import Navbar from './Navbar.vue'
import Contact from './Contact.vue'
import { topics } from './Topics.js'
import ReactionBar from './Reaction.vue'

export default {
    name: 'BlogFour',
  data() {
    return {
      topics,
    };
  },
  components: {
    Navbar,
    ReactionBar,
    Contact
  }
}
</script>

<template>
  <div>
    <Navbar/>
    <div class="centered-container">
        <div class="topic-container">
        <ul class="topic-list">
            <li class="td">
                <div class="topic-title">{{ topics[3].title }}</div>
                <div class="posted-date">{{ topics[3].postedDate }}</div>
            </li>
        </ul>
        <section class="post-content">
                <p>
                        For a long time, <a href="https://talk.hyvor.com" target="_blank" rel="noopener noreferrer">Hyvor Talk</a>
                        ran on VPS servers directly. All the dependencies were installed directly (PHP, PHP extensions, Node, etc) on the server. However, this became quite cumbersome. Upgrading was hard. The application was fragile due to inconsistencies between the environments it runs on. There are solutions like <a href="https://www.ansible.com/" target="_blank" rel="noopener noreferrer">Ansible</a>
                        to automate that, but we decided to use <a href="https://docker.com/" target="_blank" rel="noopener noreferrer">Docker</a>
                        to containerize the application. I’ll write a different tutorial on how I containerized our Laravel application. Here, I will focus on a specific problem: <strong>zero-downtime docker-compose deployments</strong>
                        .
                    </p>
                    <p>After dockerizing the application, I was looking for solutions to deploy this. The easiest one was docker-compose, which comes with Docker. It ran our application without any issues… until we needed to deploy a new version.</p>
                    <p>The major problem with docker-compose is that when we update a container, the first one is stopped and then the second one is started. This causes a downtime - in our case, a 10-20 seconds downtime between deployments.</p>
                    <p>
                        Maybe it’s time to use a container orchestration tool like <a href="https://docs.docker.com/engine/swarm/" target="_blank" rel="noopener noreferrer">Docker Swarm</a>
                        or <a href="https://kubernetes.io/" target="_blank" rel="noopener noreferrer">Kubernetes</a>
                        ? I kept them as the last resort and started finding a way to make it work with just docker-compose. I also didn’t want to write a 100-line shell script to make this work.
                    </p>
                    <p>
                        Google and ChatGPT gave very complex solutions, and most of them did not work. I found a better solution at Hackernews - specifically, <a href="https://hn.algolia.com" target="_blank" rel="noopener noreferrer">hn.algolia.com</a>
                        .
                    </p>
                    
        </section>
        </div>
    </div>
    <ReactionBar/>
    <Contact/>
  </div>
</template>

