<script>
import Navbar from './Navbar.vue'
import Contact from './Contact.vue'
import { topics } from './Topics.js'
import ReactionBar from './Reaction.vue'

export default {
    name: 'BlogThree',
  data() {
    return {
      topics,
    };
  },
  components: {
    Navbar,
    ReactionBar,
    Contact
  }
}
</script>

<template>
  <div>
    <Navbar/>
    <div class="centered-container">
        <div class="topic-container">
        <ul class="topic-list">
            <li class="td">
                <div class="topic-title">{{ topics[2].title }}</div>
                <div class="posted-date">{{ topics[2].postedDate }}</div>
            </li>
        </ul>
        <section class="post-content">
                    <p>I recently re-wrote the <a href="https://hyvor.com/" target="_blank" rel="noopener noreferrer">HYVOR login</a>. The old app was a legacy PHP application. The new one was written in <a href="https://laravel.com/" target="_blank" rel="noopener noreferrer">Laravel</a>.</p><p>All our apps (Hyvor Talk, Hyvor Blogs, etc.) use the same auth system. So, it handles a few million internal API requests per day and saves a LOT of sessions. However, all this was handled by a 4GB, 2vCPU VPS on DigitalOcean. The CPU usage of the old app was around 15-20%.</p><p>So, I decided to use the same server capacity for the new Laravel app. However, CPU usage bumped up to 100%, making everything slower.</p><p>I knew that Laravel needs more CPU than PHP, because of the service container and Reflection Magic it does. But, this much? </p><p>Both PHP and Laravel systems worked exactly the same way except for session storage.  The old system stored sessions in the database while the new system used the file system. I just <code>ls</code> ed to the session storage directory and there were thousands of files.</p><p>I was not sure but gave it a shot. I created a table to save Laravel sessions and changed <code>SESSION_DRIVER</code> to the database (Instructions are <a href="https://laravel.com/docs/9.x/session#database" target="_blank" rel="noopener noreferrer">here</a>).</p><p>CPU usage decreased to around 20-30%!</p><p>So, it is safe to say that in larger volumes, relational databases are more optimized to save data with high I/O frequency than saving in the filesystem.</p><p>I should also mention that using in-memory key-value storage like <a href="https://redis.io/" target="_blank" rel="noopener noreferrer">Redis</a> will be more efficient than MYSQL. In our case, this requires a larger RAM to save all those millions of sessions of Hyvor Talk users. So, we will keep saving sessions in MYSQL for now. We will migrate to Redis only if required.</p>
        </section>
        </div>
    </div>
    <ReactionBar/>
    <Contact/>
  </div>
</template>

