<script>
import { mapState, mapMutations } from 'vuex';

export default {
  name: 'ReactionBar',
  computed: {
    ...mapState(['reactions', 'selectedReaction']),
  },
  methods: {
    ...mapMutations(['incrementReaction', 'decrementReaction', 'setSelectedReaction', 'resetSelectedReaction']),
    handleReactionClick(reaction) {
      if (this.selectedReaction === null) {
        this.incrementReaction(reaction);
        this.setSelectedReaction(reaction);
      } else if (this.selectedReaction === reaction) {
        this.decrementReaction(reaction);
        this.resetSelectedReaction();
      } else {
        this.decrementReaction(this.selectedReaction);
        this.incrementReaction(reaction);
        this.setSelectedReaction(reaction);
      }
    },
  },
};
</script>

<template>
  <div class="reaction-bar">
    <div
      v-for="(count, reaction) in reactions"
      :key="count"
      class="reaction-item"
      :class="{'selected': reaction === selectedReaction}"
      @click="handleReactionClick(reaction)"
    >
      {{ reaction }} {{ count }}
    </div>
  </div>
</template>

<style scoped>
.reaction-bar {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  background-color: rgba(255, 255, 255, 0.595);
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  border-radius: 20px;
}

.reaction-item {
  margin: 0 15px;
  cursor: pointer;
  font-size: 24px;
}

.selected {
  font-weight: bold;
}

</style>