<script>
import Navbar from './Navbar.vue'
import Contact from './Contact.vue'
import { topics } from './Topics.js'
import ReactionBar from './Reaction.vue'

export default {
    name: 'BlogOne',
  data() {
    return {
      topics,
    };
  },
  components: {
    Navbar,
    ReactionBar,
    Contact
  }
}
</script>

<template>
  <div>
    <Navbar/>
    <div class="centered-container">
        <div class="topic-container">
        <ul class="topic-list">
            <li class="td">
                <div class="topic-title">{{ topics[0].title }}</div>
                <div class="posted-date">{{ topics[0].postedDate }}</div>
            </li>
        </ul>
        <section class="post-content">
                <blockquote>
                    <p>
                        The problem is Meilisearch’s document-adding process is <a href="https://docs.meilisearch.com/learn/advanced/asynchronous_operations.html" target="_blank" rel="noopener noreferrer">asynchronous</a>
                        . I could not find a way to disable this behavior per instance, per index, or per API call basis.
                    </p>
                </blockquote>
                    <p>
                        So, the above test fails sometimes, because even if the documents are sent from the app to Meilisearch to be added, that process is not completed by the time we call the <code>/comments/search</code>
                        endpoint.
                    </p>
                    <p>
                        One easy solution is to use <code>sleep()</code>
                        or <code>usleep()</code>
                        before calling the API endpoint. But, we will still have a <a href="https://docs.gitlab.com/ee/development/testing_guide/flaky_tests.html" target="_blank" rel="noopener noreferrer">flaky test</a>
                        . Not good.
                    </p>
                    <p>
                        It calls Meilisearch’s <code>/tasks</code>
                        endpoint until all tasks are completed. It sleeps for <code>150ms</code>
                        between two requests. Note that it uses the <code>Meilisearch\Client</code>
                        , which comes with the <code>meilisearch/meilisearch-php</code>
                        package.
                    </p>
                    <p>
                        I have noticed (on my M1 8GB Macbook) in most cases, only one API call is made to the <code>/tasks</code>
                        endpoint. The time the HTTP request takes is enough for Meilisearch to complete up to 10 inserts. So, it is not hugely inefficient. We’ll use this solution for now for testing.
                    </p>
        </section>
        </div>
    </div>
    <ReactionBar/>
    <Contact/>
  </div>
</template>

<style scoped>
  .centered-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 81vh;
}
