  text-align: center;
  background-color: rgba(255, 255, 255, 0.595);
  padding: 10px 20px 10px 20px;
  border-radius: 10px;
  width: 60%;
}

.topic-list {
  list-style: none; /* Remove bullets from list items */
  padding-top: 10px;
  padding-left: 0; /* Remove default padding from list */
  width: fit-content;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  
}

/* Add styles for the topic title and posted date */
.topic-title {
  font-size: 24px;
  font-weight: bold;
  width: fit-content;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: #7A316F;
}

.posted-date {
  font-size: 14px;
  color: #9e4362;
  width: fit-content;
  font-weight: 500;
  font-family: cursive;
}

.td{
  padding-bottom: 15px;
  
}

.post-content{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-align: justify;
    width: fit-content;

}

.post-content code{
    color: #9129bd;
    font-weight: 600;
}

.post-content blockquote {
	padding: 1em 1.5em;
	border-left: .25em solid #461959;
    background-color: rgba(128, 128, 128, 0.426);
}

.post-content blockquote > :first-child {
	margin-top: 0;
}

.post-content blockquote > :last-child {
	margin-bottom: 0;
}

</style>