// store/index.js

import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    reactions: {
      "😍": 0,
      "😆": 0,
      "😢": 0,
      "😮": 0,
      "👍": 0,
      "👎": 0,
    },
    selectedReaction: null,
  },
  mutations: {
    incrementReaction(state, reaction) {
      state.reactions[reaction]++;
      localStorage.setItem("reactions", JSON.stringify(state.reactions)); // Save to Local Storage
    },
    decrementReaction(state, reaction) {
      if (state.reactions[reaction] > 0) {
        state.reactions[reaction]--;
        localStorage.setItem("reactions", JSON.stringify(state.reactions)); // Save to Local Storage
      }
    },
    setSelectedReaction(state, reaction) {
      state.selectedReaction = reaction;
    },
    resetSelectedReaction(state) {
      state.selectedReaction = null;
    },
    initializeStateFromLocalStorage(state) {
      const reactionsFromStorage = JSON.parse(
        localStorage.getItem("reactions")
      );
      if (reactionsFromStorage) {
        state.reactions = reactionsFromStorage;
      }
    },
  },
});
