<script>
import {topics} from './Topics.js'

export default {
  name: 'TopicList',
  data() {
    return {
      topics,
    };
  },
};
</script>

<template>
  <div class="centered-container">
    <div class="topic-container">
      <div class="topic">Blogs</div>
      <ul class="topic-list">
        <li v-for="topic in topics" :key="topic.id" class="td">
          <router-link :to="`/blog/${topic.id}`" class="topic-link">
            <div class="topic-title">{{ topic.title }}</div>
            <div class="posted-date">{{ topic.postedDate }}</div>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

